.inm-placement{
  display: flex;
  margin: 100px 40px;
  
}

.inm-div{
  background-color: #F7F7F7;
}

.inm-spacer {
  height: 70px;
  width: 100%;
}

.inm-title-div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 100px;
  flex-wrap: wrap;
}

.inm-pretitle {
  margin-bottom: -7px;
  font-size: 13px;
  font-weight: 400;
}

.inm-title {
  font-size: 2.2em;
  font-weight: 400;
}

.inm-title-left{
  margin: 50px 0;
}

.inm-subtitle {
  font-size: 1em;
  font-weight: 400;
}

.inm-title-right{
  border-left: 1px solid rgb(189, 189, 189);
  padding-left: 16px;
}

.inm-type{
  color: #A41A1A;
  font-size: 1.6em;
  font-weight: 400;
}

.inm-price{
  font-size: 1.3em;
}

.inm-slider{
  margin: 0 100px;
}

.inm-body{
  margin-right: 50px;
  padding: 50px;
  border: 1px solid rgb(204, 204, 204);
  box-shadow: 0px 0px 20px #e9e9e9;
}

.inm-body-char{
  display: flex;
}

.inm-body-char-div{
  margin: 20px 35px 20px 0;
}

.inm-char-info{
  display: flex;
  align-items: center;
}

.inm-body-att{
  margin: 50px 0;
  display: flex;
  flex-wrap: wrap;
}

.inm-att-title{
  margin-top: 50px;
  font-weight: 400;
  font-size: 2em;
}

.inm-body-att-div {
  height: 50px;
  width: 50%;
  max-width: 50%;
}

.inm-char-icon{
  margin-right: 4px;
}

.inm-desc-title{
  font-size: 2em;
  font-weight: 400;
  margin: 30px 0 20px;
}

.inm-sidebar{
  border: 1px solid rgb(204, 204, 204);
  box-shadow: 0px 0px 20px #e9e9e9;
  align-self: flex-start;
}

.inm-agent-info{
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.inm-agent-img{
  width: 150px;
  height: 150px;
  margin-top: -40px;
  margin-bottom: 20px;
}

.c-loader {
  width: 50px;
  height: 50px;
  border: 6px solid #e5e5e5;
  border-top-color: #51d4db;
  display: block;
  animation: is-rotating 1s infinite;
  border-radius: 50%;
}

@keyframes is-rotating {
  to {
    transform: rotate(1turn);
  }
}
